html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

p {
  font-size: 2rem;
  line-height: 1.5em;
}

.contenedor {
  width: 95%;
  max-width: 130rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
  margin: 0;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  display: block;
  width: 100%;
  height: auto;
}

.contenedor__grid {
  display: grid;
  grid-template-columns: 3fr 1fr;
}

.header {
  display: flex;
  justify-content: space-between;
  background-color: grey;
  padding: 1rem;
  border-bottom: 2px solid black;
  box-shadow: inset 0px -10px 5px rgba(0, 0, 0, 0.3);
}
.header :last-child {
  align-items: center;
}
.header :first-child {
  align-items: end;
}
.header h1 {
  font-size: 5.5rem;
}
.header .navegacion {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  font-size: 2rem;
  text-align: center;
}
.header .navegacion a {
  margin: 0 0.8rem 0 0;
  color: black;
  padding: 2rem;
}
.header .navegacion a:hover {
  box-shadow: inset -2px -2px 15px rgba(255, 255, 255, 0.8);
}
.header .navegacion :last-child {
  margin: 0;
}
.header .logotipo {
  display: flex;
  padding: 2rem;
}

sidebar {
  display: grid;
  grid-template-columns: 1fr;
  height: 80rem;
  background-color: grey;
}
sidebar a {
  font-size: 2rem;
}

.entrada__sidebar {
  border: 1px solid white;
  padding: 1rem;
  margin: 0 0;
}
.entrada__sidebar p {
  margin: 0 0;
}

.contenido {
  width: 50rem;
  height: 60rem;
}